.email-blue {
	color: #0d6efd;
}

.contact-image {
	width: 45px;
	height: 45px;
}

.contact-item {
	&-enter {
		opacity: 0;

		&-active {
			opacity: 1;
			transition: opacity 400ms ease-in;
		}
	}

	&-exit {
		opacity: 1;

		&-active {
			opacity: 0;
			transition: opacity 400ms ease-in;
		}
	}
}